import { Link, PageProps } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import React, { FC } from 'react';
import * as style from './index.module.scss';
import SEO from '../../components/SEO';


const CardPage = () => {
    return (
        <>
            <div
                className={style.div}
            >
                <main>
                    <Link
                        to='/'
                        className={`${style.link} ${style.main}`}
                        title='Personal Website | SNinjo'
                    >
                        <StaticImage
                            className={style.image}
                            src='../../assets/SNinjo.png'
                            alt='SNinjo logo'
                            loading='eager'
                        />
                        <h1>SNinjo</h1>
                        <span>Software Designer</span>
                    </Link>
                    <hr />
                    <a
                        className={`${style.link} ${style.secondary}`}
                        href="https://www.linkedin.com/in/ru-jun-liao-3bba07196/"
                        target="_blank"
                        rel="noreferrer noopener"
                        title='LinkedIn | Ru-Jun Liao'
                    >
                        <h1>Ru-Jun Liao</h1>
                        <span>contact@SNinjo.com</span>
                        <StaticImage
                            className={style.image}
                            src='../../assets/personalPhoto02.jpg'
                            alt='the personal photo of website owner - SNinjo'
                            loading='eager'
                        />
                    </a>
                </main>
            </div>
        </>
    )
}
export default CardPage;

export const Head: FC<PageProps> = ({ location }) => (
    <SEO
        title='Card'
        pathname={location.pathname}
    />
)
